<template>
    <div id="added">
        <Header2  :FlagSignin=false :FlagAcc=false :FlagEdit=false :FlagAdded=true />
        <div class="content">
            <div class="con">
                <div class="con1">
                    <input type="text" placeholder="请填写你的姓名" v-model="name" v-on:input="name2()" />
                    <p v-if="show1">请输入正确的姓名</p>
                </div>
                <div class="con1">
                    <input type="text" placeholder="请选择你的地址" v-model="address" @click="address2()" />
                </div>
                <div class="con1">
                    <input type="text" placeholder="请填写详细送餐地址" v-model="detailed" v-on:input="detailed2()" />
                    <p v-if="show2">请输入正确的详细地址</p>
                </div>
                <div class="con1">
                    <input type="text" placeholder="请填写能够联系到您的手机号" v-model="phone" v-on:input="phone2()" />
                    <p v-if="show3">请输入正确的手机号</p>
                </div>
                <div class="con1">
                    <input type="text" class="inp" placeholder="备用联系电话（选填）" v-model="standby" v-on:input="standby2()" />
                    <p v-if="show4">请输入正确的联系电话</p>
                </div>
            </div>
            <button class="btn" @click="add()">新增地址</button>
        </div>
    </div>
</template>

<script>
import { Dialog } from 'vant';
import Header2 from '../components/Header2' 
export default {
  components:{
        Header2, [Dialog.Component.name]: Dialog.Component
    },
  name: 'Edit',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      name:'',
      address:'',
      detailed:'',
      phone:'',
      standby:'',
      show1:false,
      show2:false,
      show3:false,
      show4:false
    }
  },
  created(){

  },
  methods:{
        name2(){
            if(this.name==''){
                this.show1=true
            }else{
              this.show1=false
            }
        },
        address2(){
            this.$router.push('./Newaddress')
        },
        detailed2(){
            if(this.detailed.length<5 || this.detailed==''){
                this.show2=true
            }else{
              this.show2=false
            }
        },
        phone2(){
            // var zheng=/^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/;
            // console.log(zheng)
            if(this.phone.length != 11 || this.phone=='' ){
                this.show3=true
            }else{
              this.show3=false
            }
        },
        standby2(){
            if(this.standby.length != 11 || this.standby=='' ){
                this.show4=true
            }else{
              this.show4=false
            }
        },
        add(){
            if(this.name=="" || this.address=="" || this.detailed==""  || this.phone=="" || this.standby=="" ){
                Dialog.confirm({
                    title: '错误',
                    message: '请输入正确内容'
                    }).then(() => {
                    // on confirm
                    }).catch(() => {
                    // on cancel
                });
               
                
            }else{
                
                this.axios.post(' http://elm.cangdu.org/v1/users/38184/addresses',{
                address: this.address,
                address_detail: this.detailed,
                geohash: "31.22299,121.36025",
                name: this.name,
                phone: this.phone,
                phone_bk: this.standby,
                poi_type: 0,
                sex: 1,
                tag: "公司",
                tag_type: 4,
                }).then(res=>{
                    console.log(res.data)
                    this.$router.push('/Edit')
                })
            }
        }
  },
  mounted(){
     this.address=this.$route.query.name
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#added{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #F4F5F9;
    position: fixed;
    top: 0;
    background: #fff;
    z-index:5;
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        margin-top: 2rem;
        .con{
             width: 100%;
            background: white;
            margin-top: 0.3rem;
            .con1{
                 width: 90%;
                 margin-left: 5%;
                input{
                    width: 100%;
                    height: 1.3rem;
                    margin-top: 0.3rem;
                    border-radius: 5px;
                    background: #F2F2F2;
                    border: none;
                    padding-left: 2%;
                }
                .inp{
                    margin-bottom: 0.4rem;
                }
                p{
                    color: red;
                    font-size: 0.3rem;
                    line-height: 1rem;
                }
            }
        }
        .btn{
            margin-top: 0.5rem;
            line-height: 1.5rem;
            width: 90%;
            border-radius: 5px;
            margin-left: 5%;
            background: #4CD964;
            color: #B5EFBF;
            border: none;
        }
    }
    
}
    
</style>



